//vuex 
//1.vue
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
	//
	state: { //vuex
		userinfo: uni.getStorageSync('USER_INFO')||{},
		historyLists: uni.getStorageSync("_history") || []
	},
	mutations: {
		SET_USER_INFO(state, userinfo) {
			state.userinfo = userinfo
		},
		SET_HISTORY_LIST(state, history) { //state    history
			state.historyLists = history //historyList = history
		},
		CLEAR_HISTORY_LIST(state) {
			state.historyLists = []
		}
	},
	actions: { //  mutations    actions
		set_userinfo({
			commit
		}, userinfo) {
			uni.setStorageSync('USER_INFO',userinfo)
			commit('SET_USER_INFO', userinfo)
		},
		set_history({
			commit,
			state
		}, history) { //commit mutations  history
			let list = state.historyLists
			list.unshift(history)
			uni.setStorageSync('_history', list)
			commit('SET_HISTORY_LIST', list)
		},
		clear_history_list({
			commit
		}) {
			uni.removeStorageSync('_history')
			commit('CLEAR_HISTORY_LIST')
		}
	}
})

export default store
